import { COMMENT_NODE_KINDS, CONTAINER_NODE, NodeKinds } from './constance';
/**
 * question node
 */
var isQuestionNode = function (node) {
    return node.kind === NodeKinds.QUESTION_NODE;
};
/**
 * block node
 */
var isBlockNode = function (node) {
    return node.kind === NodeKinds.BLOCK_NODE;
};
/**
 * loop node
 */
var isLoopNode = function (node) {
    return node.kind === NodeKinds.LOOP_NODE;
};
/**
 * execution node
 */
var isExecutionNode = function (node) {
    return node.kind === NodeKinds.EXECUTION_NODE;
};
/**
 * market node
 */
var isMarkNode = function (node) {
    return node.kind === NodeKinds.MARK_NODE;
};
/**
 * quota node
 */
var isQuotaNode = function (node) {
    return node.kind === NodeKinds.QUOTA_NODE;
};
/**
 * display node
 */
var isDisplayNode = function (node) {
    return node.kind === NodeKinds.DISPLAY_NODE;
};
/**
 * exit node
 */
var isExitNode = function (node) {
    return node.kind === NodeKinds.EXIT_NODE;
};
/**
 * [BlockNode,LoopNode]
 */
var isContainerNode = function (node) {
    return CONTAINER_NODE.includes(node.kind);
};
/**
 * 
 */
var isCommentNode = function (node) {
    return COMMENT_NODE_KINDS.includes(node.kind);
};
/**
 * option item
 */
var isOptionItem = function (item) {
    return item.kind === 'OptionItem';
};
/**
 * option list
 */
var isOptionList = function (item) {
    return item.kind === 'OptionList';
};
/**
 * html element
 */
var isHtmlElement = function (item) {
    return item.kind === 'HtmlItem';
};
/**
 * script content
 */
var isScriptContent = function (item) {
    return item.kind === 'ScriptContent';
};
export { isQuestionNode, isBlockNode, isLoopNode, isExecutionNode, isMarkNode, isQuotaNode, isDisplayNode, isExitNode, isContainerNode, isCommentNode, isOptionItem, isOptionList, isHtmlElement, isScriptContent, };
